body, html {
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  margin: 0 auto;
  margin-top: 40px;
  width: 700px;
  border: 1px solid dodgerblue;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0px 0px 5px 0px rgba(30, 144, 255, 0.6);
}

.app {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  color: #1e8eff;
  text-shadow: 1px 1px 1px dodgerblue;
}

.title i {
  font-size: 44px;
}

.btn {
  height: 40px;
  width: 120px;
  font-size: 15px;
  color: white;
  border-radius: 15px;
  background: transparent;
  background: dodgerblue;
  position: relative;
  transition: 0.5s;
  outline: none;
}

.btn:after {
  display: block;
  content: "";
  width: 10px;
  height: 10px;
  background: white;
  position: absolute;
  border-radius: 50%;
  top: calc(50% - 5px);
  left: 12px;
}

.btn:hover {
  transform: scale(1.05);
  background: white;
  color: dodgerblue;
}

.btn:hover:after {
  background: dodgerblue;
}

.button_group {
  margin-top: 15px;
}

.text_input {
  font-size: 18px;
  width: 375px;
  height: 35px;
  border-radius: 15px;
  border: 1px solid dodgerblue;
  padding: 3px 0px 3px 6px;
  margin: 5px 1px 3px 0px;
}

.text_input:focus {
  box-shadow: 0 0 5px #51cbee;
  border: 1px solid #51cbee;
  outline: none;
}

.task_list {
  list-style: none;
  margin-left: 0;
  padding-left: 0px;
  width: 650px;
}

.list_element {
  min-height: 62px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid white;
  background: dodgerblue;
  color: white;
  margin: 5px 1px 3px 0px;
  border-radius: 15px;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.list_element .list_element_left {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
}

.list_element .list_element_left span {
  left: 1px;
  position: absolute;
  margin-right: 15px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: dodgerblue;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.list_element .list_element_left .editable_par {
  margin-left: 45px;
}

.icon_container {
  display: flex;
}

.btn_icon {
  color: white;
  background: dodgerblue;
  border: none;
  outline: none;
  cursor: pointer;
  width: 40px;
  margin-right: 20px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}

.btn_icon i {
  font-size: 23px;
}

.btn_icon:hover {
  transform: scale(1.05);
  background: white;
  color: dodgerblue;
}

.btn_icon:hover:after {
  background: dodgerblue;
}

.editable_par {
  padding: 10px;
  border-radius: 15px;
  margin: 10px;
}
